*{
    box-sizing: border-box;
}

body{
    background-color: gray;
    padding: 0;
    margin: 0;
    height: 100%;

}

li,ul,button{
    list-style: none;
}


/* container */
html,
body,
#root,
.container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    font-size: 95px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }

.movies{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 90%;
}

.movies .movie {
    width: 45%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 20px;
    margin-bottom: 70px;
    background-color: white;
    border-radius: 5px;
    color: #adaeb9;
    font-weight: 300;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}  
  .movie img {
    position: relative;
    top: -50px;
    max-width: 160px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
      0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
  
.movie .movie__title,
.movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}
  
.movie .movie__genres {
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0px;
}

.movie__genres li,
.movie .movie__year {
  margin-right: 10px;
  font-size: 14px;
}

@media screen and (max-width:768px) {
    .loader{
        font-size: 20px;
    }
    .movies{
        width: 100%;
    }
    .movie img{
        width: 100%;
        top: 0; 
        cursor: pointer;
        margin: auto;
    }
    .movie img:hover{
        opacity: 0.8;
        transition: all 200ms ease-in;
    }
    .movie__year,
    .genres__genre,
    .movie__sumarry,
    .movie__title{
        display: none;
    }
}